const fromAssetsFolder = (img) => {
  const isDevelopment =
    location.host.startsWith("192") ||
    location.host.startsWith("local") ||
    location.host.startsWith("rbs-components");

  if (isDevelopment) {
    // DE: static assets should not be part of the bundle?
    return `./static/` + img;
  } else {
    console.warn(`Fix svgIconPath() on Production IMG:`, img);
    return "../../../" + img;
  }
};

export default { fromAssetsFolder };
