<template>
  <v-card class="mx-auto overflow-hidden" height="auto">
    <h3><img src="https://i.imgur.com/GE0u649.png" style="max-height:300px;"></h3>
    <h3>Met standaard Vuetify Tabs</h3>
    <ul>
      <li>
        Met CSS hack om de hoogte te beperken (56px logo hoogte)... dan werkt de
        Settings knop rechts niet meer...
      </li>
      <li>Hoe kan Settings > Account settings default opengeklapt worden?</li>
    </ul>
    <v-app-bar
      xabsolute
      color="#363636"
      dark
      xshrink-on-scroll
      dense
      xprominent
      xscroll-target="#scrolling-techniques-4"
    >
      <div class="nav-logo mt-4 ml-1">
        <a href="/">
          <img
            height="45"
            width="45"
            :src="fromAssetsFolder('roundabout_logo.png')"
          />
        </a>
      </div>
      <!-- <v-app-bar-nav-icon></v-app-bar-nav-icon> -->

      <!-- <v-toolbar-title>Roundabout Social Dashboard</v-toolbar-title> -->

      <v-spacer></v-spacer>
      <!-- <v-btn icon>
        <v-icon>mdi-magnify</v-icon>
      </v-btn> -->

      <v-menu>
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon v-bind="attrs" v-on="on">
            <v-icon>mdi-account-cog</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-group
            v-for="item in navigation.settings"
            :key="item.text"
            v-model="item.active"
            :prepend-icon="item.action"
            no-action
            :class="item.submenu ? 'hassubmenu' : 'nosubmenu'"
            :to="true"
          >
            <template v-slot:activator>
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title>{{ item.text }}</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </template>

            <v-list-item v-for="subItem in item.submenu" :key="subItem.title">
              <!-- @click="" -->
              <v-list-item-content>
                <v-list-item-title>{{ subItem.text }}</v-list-item-title>
              </v-list-item-content>

              <v-list-item-action>
                <v-icon>{{ subItem.action }}</v-icon>
              </v-list-item-action>
            </v-list-item>
          </v-list-group>
        </v-list>
      </v-menu>

      <template v-slot:extension>
        <v-tabs icons-and-text centered>
          <template v-for="tab in navigation.tabs">
            <!-- <router-link
              :to="tab.url"
              @click.native="setActiveUrl(tab.url)"
              :key="tab.text"
              v-if="!tab.submenu"
            > -->
            <v-tab :key="tab.text" href="/#navhor">
              {{ tab.text }}
              <v-icon>{{ tab.icon }}</v-icon>
            </v-tab>
            <!-- </router-link> -->
          </template>
        </v-tabs>
      </template>
    </v-app-bar>
    <!-- <h3>Horizontal items</h3> -->
  </v-card>
</template>

<script>
import { mapActions, mapState } from "vuex";

import fromAssetsFolder2 from "./../utils";

export default {
  name: "Navigation",
  components: {},
  data() {
    return {
      // data structure van verticale navigatie aangepast
      navigation: {
        tabs: [
          {
            icon: "mdi-account-group-outline",
            text: "Community",
            url: "/community",
            active: false,
          },
          // {
          //   icon: "mdi-chat",
          //   text: "Chat",
          //   url: "/chat",
          //   active: false,
          // },
          {
            icon: "mdi-trending-up",
            text: "Your Campaigns",
            url: "/navhor",
            active: false,
          },
          {
            icon: "mdi-radar",
            text: "Your Audience trends",
            url: "/navhor",
            active: false,
          },
        ],
        settings: [
          {
            icon: "mdi-movie-outline",
            text: "Video Library",
            url: "/videolibrary",
            active: false,
          },
          {
            icon: "mdi-help-circle",
            text: "FAQ",
            url: "/faq",
            active: false,
          },
          {
            icon: "mdi-chevron-up",
            "icon-alt": "mdi-chevron-down",
            text: "Account settings",
            active: false,
            model: false,
            submenu: [
              {
                text: "Settings",
                url: "/account",
                icon: "mdi-tune",
                active: false,
              },
              {
                text: "Notification",
                url: "/notification/devices",
                icon: "mdi-bell-outline",
                active: false,
              },
              {
                text: "Social Media",
                url: "/account/settings/socialmedia",
                icon: "mdi-share-variant-outline",
                active: false,
              },
            ],
          },
        ],
      },
    };
  },
  computed: {
    ...mapState({
      totalUnseenChatMessages: "totalUnseenChatMessages",
    }),

    user: function () {
      return this.$store.state.user;
    },
  },
  methods: {
    fromAssetsFolder(img) {
      console.log(img);
      return "./static/" + img;
      return fromAssetsFolder2(img);
    },
    setActiveUrl(userClick) {
      console.log(userClick);
    },
  },
  mounted() {
    //this.setActiveUrl();
  },
};
</script>
<style>
/* header {
  --forceHeaderHeight:56;
  max-height: calc( 1px * var(--forceHeaderHeight));
}
.v-toolbar__extension{
  margin-top:calc( -1px * calc( var(--forceHeaderHeight) - 16 ));
} */
h3 {
  margin-top: 1em;
  margin-bottom: 0.5em;
}
.nosubmenu .v-icon {
  /* instead of adding loads of v- if a menu item has no submenus, just hide the icon.. */
  display: none;
}
</style>

